import React,{useState,useEffect,useRef} from 'react'
import { Link } from 'react-router-dom'
import {Container,Row,Col} from 'reactstrap'
import {FaAngleLeft,FaAngleRight}  from "react-icons/fa"
import NoImage from '../../../../images/No_image.png'
import axios from 'axios'



const IndexList = props => {
  const {url,type} = props
  const [content,setContent] = useState([])
  const [current,setCurrent] = useState(0)
  const [scrollLeft,setScrollLeft] = useState(0)
  const [scrollWidth,setScrollWidth] = useState(0)
  const [clientWidth,setClientWidth] = useState(1)
  const rowRef = useRef(null)

  const rowHandler = direction =>{
    if(rowRef.current){
      const {scrollLeft,clientWidth} = rowRef.current
      const scrollTo = direction === 'left' ? scrollLeft - clientWidth : scrollLeft + clientWidth
      rowRef.current.scrollTo({left:scrollTo,behavior:'smooth'})
    }
    
  }
  
  const scrollCalc = (target)=>{
    
    if(target.scrollLeft > scrollLeft){
      // Right
      setScrollWidth(target.scrollWidth)
      setClientWidth(target.clientWidth)
    }
    // Right+left && left
    setScrollLeft(target.scrollLeft)

  }

  useEffect(()=>{
    axios.get(url).then(result=>{
      const list = (result.data.results).map(i=>{
        i['type']=type
        return i
      })
      setContent(list)
    }).catch(err=>{
      console.log(err)
    })
  },[url])
  
  const movieStyle = {
    transform : `translateX(${current}px)`
  }
  return (
    <div className='indexList d-flex flex-row flex-nowrap justify-content-center mt-3'>
      <span className={scrollLeft <= 0 && 'opacity'} onClick={()=>rowHandler('left')}>
        <FaAngleLeft></FaAngleLeft>
      </span>
      <div className="overflowMovie" ref={rowRef} onScroll={e=>scrollCalc(e.target)}>
        <div className='listMovie' >
          { content.length !== 0 ? content.map((i,index)=>{
              const style = {
                backgroundImage : `${i['poster_path'] === null || !i['poster_path']? `url(${NoImage})` : `linear-gradient(180deg, rgba(22, 22, 22, 0) 30.58%, rgba(22, 22, 22, 0.98) 100%),url(${process.env.REACT_APP_ImageUrl}${i['poster_path']})`}`
              }

              return  <Link key={index} to={`../movieDesc/${i['id']}/${i['type']}`} className='listItem mr-3'>
                          <div style={style}>
                            <span>{(i['vote_average']).toFixed(1)}</span>
                          </div>
                          <span>
                            {i['title'] || i['name']}
                          </span>
                      </Link>
            }): '查無相關影片'
          }
        </div>
      </div>
      <span className={(scrollLeft + clientWidth) === scrollWidth && 'opacity'} onClick={()=>rowHandler('right')}>
        <FaAngleRight></FaAngleRight>
      </span>
    </div>
  )
}

export default IndexList