import React,{useEffect,useState}from 'react'
import { useParams} from "react-router-dom"
import {Container,Row,Col} from 'reactstrap'
import Nav from '../Nav'
import MainDesc from './MainDesc'
import IndexList from '../index/indexList'
import Cast from './Cast'
import axios from 'axios'


const Main = props => {
  const [details,setDetails] = useState([])
  const [sub,setSub] = useState([])
  const [checkOverview,setCheckOverview] = useState(false)
  const [genres,setGeners] = useState([])
  const [language,setLanguage] = useState([])
  const [director,setDirector] = useState([])
  const [provider,setProvider] = useState([])
  const [cast,setCast] = useState([])

  const {id,type}= useParams()
  useEffect(()=>{
    const axiosDetails = axios.get(`${process.env.REACT_APP_Url}/${type}/${id}?api_key=${process.env.REACT_APP_Key}&language=zh-TW`).then(res=>{
      res.data['type'] = type
      return res
    })
    const axiosDirector = axios.get(`${process.env.REACT_APP_Url}/${type}/${id}/credits?api_key=${process.env.REACT_APP_Key}&language=zh-TW`)
    const axiosProvider = axios.get(`${process.env.REACT_APP_Url}/${type}/${id}/watch/providers?api_key=${process.env.REACT_APP_Key}`)

    axios.all([axiosDetails,axiosDirector,axiosProvider]).then(axios.spread((...res)=>{
      const itemDetails = res[0].data
      const itemCastCrew = res[1].data
      const itemProvider = res[2].data
      setSub(itemDetails['overview'].ellipsis(60))
      setCheckOverview(itemDetails['overview'].length > 60 ? true : false)
      setDetails(itemDetails)
      setGeners(itemDetails['genres'])
      setLanguage((itemDetails['spoken_languages']).map(i=>languageMap(i['iso_639_1'])))
      
      setCast(itemCastCrew['cast'].slice(0,10))
      setDirector(itemCastCrew['crew'].length !== 0 ? itemCastCrew['crew'].filter(i=>{
        const job = i['job']
        return job === 'Director' || job === 'Executive Producer' || job === 'Animation Director'
      }): [])
      setProvider('TW' in itemProvider['results']?itemProvider['results']['TW'] : [])

    })).catch(err=>{
      console.log(err)
    })
  },[id])

  const languageMap = str =>{
    const map = {
      'ar':'阿拉伯語',
      'en':'英語',
      'fr':'法語',
      'de':'德語',
      'ru':'俄語',
      'hi':'印地語',
      'it':'義大利語',
      'la':'拉丁語',
      'es':'西班牙語',
      'fi':'芬蘭語',
      'el':'希臘語',
      'tl':'菲律賓語',
      'ja':'日語',
      'ko':'韓語',
      'zh':'中文',
      'default':'未知'
    }
    return map[str] || map['default']
  }



  return (
    <>
      <div className='mainBG'>
        <Nav/>
        <MainDesc details={details} genres={genres} language={language} provider={provider} director={director} subOverview={checkOverview ? sub : false}/>
        <Cast cast={cast} bg={true}/>
        <Container className='mt-5 detailCardBG indexListContainer'>
          <Row>
            <Col md={12} >
              <h4 className='text-white mb-5'>相關影片</h4>
              <IndexList url={`${process.env.REACT_APP_Url}/${type}/${id}/similar?api_key=${process.env.REACT_APP_Key}&language=zh-TW&page=1`} type={type} />
            </Col>
          </Row>
        </Container>
      </div>
    </>
  )
}

export default Main