import React,{useEffect} from 'react'
import {Container,Row,Col} from 'reactstrap'
import Type_render from './Type_render'

const Filter = props => {
  const {type,filter,setting,check,current} = props
  const filterList = [{
    id:0,
    name:'人氣'
  },{
    id:1,
    name:'評分'
  },{
    id:2,
    name:'上映日'
  },{
    id:3,
    name:'片名'
  }]
  useEffect(()=>{
    setting(0)
  },[type])

  return (
    <Container className='mt-4 cardBG'>
      <Row>
        <Col md={12}>
          <Type_render content={filterList} select={filter} setting={setting} check={check} current={current}/>
        </Col>

      </Row>
    </Container>
  )
}

export default Filter