import React  ,{useState,useEffect}from 'react'

const AppleBG = props => {
  const {progress} = props
  console.log(progress)
  const style ={
    '--scale':progress
  }
  return (
    <section className='appleBG'>
      <section className="section d-flex justify-content-center align-items-center h2">開始</section>
      <section style={style} className={progress > 1 ? "section sticky" : "section"}>
        <div className="phone">
          <img src="https://picsum.photos/1920/1200?grayscale" alt="" />
          <div className="rect">
          
          </div>
        </div>
      </section>
      <section className={progress > 1 ? "section static" : "section"}>  
        <div className="phone">
          <img src="https://picsum.photos/1920/1200?grayscale" alt="" />
          <div className="rect">
          
          </div>
        </div>
      </section>
      <section className="section d-flex justify-content-center align-items-center h2">結束</section>
    </section>
  )
}

export default AppleBG