import React,{useEffect,useState,useCallback} from 'react'
import {Container,Row,Col} from 'reactstrap'
import axios from 'axios'
import Type_render from './Type_render'
import RateBar from './RateBar'

const Type = props => {
  const {type,searchCallback,rateBar,setRateBar,rateMove,setRateMove} = props
  const [typeSelect,setTypeSelect] = useState([])
  const [time,setTime] = useState([])
  const [selectIndex,setSelectIndex] = useState(0)
  const [selectTime,setSelectTime] = useState(0)



  useEffect(()=>{
    const axiosType = axios.get(`${process.env.REACT_APP_Url}/genre/${type}/list?api_key=${process.env.REACT_APP_Key}&language=zh-TW`)

    axios.all([axiosType]).then(axios.spread((...res)=>{
      const resType = [{id:0,name:'全部'}].concat(res[0].data['genres'])
      
      setTypeSelect(resType)
      // init State
      setSelectIndex(0)
      setSelectTime(0)
    }))
  },[type])
  
  useEffect(()=>{
    const runTime = ()=>{
      let timeArray = Array.from({ length: 13 })
      const year = (new Date()).getFullYear()
      const yearArray = timeArray.map((i,index)=>{
        return {
                id:year - index,
                name:year - index
                }
      })
      const result = [{id:0,name:'全部'}].concat(yearArray)
      setTime(result)      
    }
    runTime()
  },[])
  return (
    <Container className='mt-5 cardBG'>
      <Row>
        <Col md={10}>
          <Type_render title={'類型'} content={typeSelect} select={selectIndex} setting={setSelectIndex}/>
          <Type_render title={'時間'} content={time} select={selectTime} setting={setSelectTime}/>
          <RateBar  rateBar={rateBar} setRateBar={setRateBar} rateMove={rateMove} setRateMove={setRateMove}/>
        </Col>
        <Col md={2} className='d-flex align-items-end'>
          <button className='theaterSearchBtn w-100' onClick={()=>searchCallback(selectIndex,selectTime,rateBar)}>搜尋</button>
        </Col>
      </Row>
    </Container>
  )
}

export default Type