import React,{useState,useEffect,useCallback} from 'react'
import { useParams } from 'react-router'
import { Container,Row } from 'reactstrap'
import Nav from '../Nav'
import Type from './Type'
import Filter from './Filter'
import RenderResult from '../RenderResult'
import axios from 'axios'

const Main = () => {
  const {type}= useParams()
  const [search,setSearch] = useState(0)
  const [voteAverage,setVoteAverage] = useState(0)
  const [selectIndex,setSelectIndex] = useState('')
  const [selectTime,setSelectTime] = useState('')
  
  const [rateBar,setRateBar] = useState(0)
  const [rateMove,setRateMove] = useState(35)

  const [filter,setFilter] = useState(0)
  const [check,setCheck] = useState(0)
  const [totalPage,setTotalPage] = useState(1)
  const [current,setCurrent] = useState(1)
  const [content,setContent] = useState([])
  const [loading,setLoading] = useState(false)

  const submitSearch = useCallback((index,year,rate) =>{
    setSelectIndex(index === 0 ? '' : index)
    setSelectTime(year === 0 ? '' : year)
    setVoteAverage(rate)
    setSearch(search + 1)
    setCheck(false)
  },[voteAverage,selectIndex,selectTime])

  const initNav = useCallback(()=>{
    setCheck(false)
    setCurrent(1)
    setVoteAverage(0)
    setSelectIndex('')
    setSelectTime('')
    setRateBar(0)
    setRateMove(35)
  },[check,current,voteAverage,selectIndex,selectTime,rateBar,rateMove])

  useEffect(()=>{
    const getContent = ()=>{
      axios(`${process.env.REACT_APP_Url}/discover/${type}?api_key=${process.env.REACT_APP_Key}&language=zh-TW&region=TW&sort_by=${filter === 0 ? 'popularity' : filter === 1 ? 'vote_count' : filter === 2 ? 'release_date' : 'original_title'}.desc&page=${current}&year=${selectTime}&vote_average.gte=${voteAverage}&vote_average.lte=10&with_genres=${selectIndex}&watch_region=TW`).then(res=>{
        const result = res.data
        if(!check){
          setContent(result.results)
          setCheck(true)
        }else{
          setContent([...content,...result.results])
        }
        // console.log(content)
        setTotalPage(result.total_pages)
        setLoading(false)
    }).catch(err=>{
        console.log(err)
      })
    }
    getContent()
    
  },[type,filter,current,search])

  return (
    <>
      <div className='mainBG'>
        <Nav select={type} hover={true} initTheater={initNav} />
        <Type type={type}  searchCallback={submitSearch}  rateBar={rateBar} setRateBar={setRateBar} rateMove={rateMove} setRateMove={setRateMove}/>
        <Filter type={type} filter={filter} setting={setFilter} check={setCheck} current={setCurrent}/>
        <Container>
          <Row>
            <RenderResult content={content} type={type} loading={loading} totalPage={totalPage} current={current} setCurrent={setCurrent}/>
          </Row>
        </Container>
      </div>
    </>
  )
}

export default Main