import React from 'react'
import { Col } from 'reactstrap'
import { Link } from 'react-router-dom'
import Cast from './movieDesc/Cast'
import NoImage from '../../../images/No_image.png'

const RenderResult = props => {
  const {content,target,type,loading,totalPage,current,setCurrent} = props
  return (
    <>
      <Col md='12' className='searchContainer mt-3'>
        {
          content.length !== 0 ? (!target || type === 'cast' ? content.map((i,index)=>{
            const style = {
              backgroundImage : `${i['poster_path'] === null || !i['poster_path']? `url(${NoImage})` : `linear-gradient(180deg, rgba(22, 22, 22, 0) 30.58%, rgba(22, 22, 22, 0.98) 100%),url(${process.env.REACT_APP_ImageUrl}${i['poster_path']})`}`
            }
            return  <Link key={index} to={`../movieDesc/${i['id']}/${i['media_type'] || type}`} className='searchItem mx-3 my-3'>
                        <div style={style}>
                          <span>{i['vote_average'] ? (i['vote_average']).toFixed(1) : 0}</span>
                        </div>
                        <span>
                          {i['title'] || i['name']}
                        </span>
                    </Link>
          }) : <Cast cast={content} bg={false} center={true}/> ) : <h3>查無資料</h3>
        }
      </Col>
      <Col md={12} className='d-flex justify-content-center'>
        {type !== 'cast' && totalPage !== current && <button className='btnStyle my-5' onClick={()=>{
          setCurrent(current + 1)
        }}>{loading ? 'Loading...' : 'Load More'}</button>}
      </Col>

    </>

  )
}

export default RenderResult