import React,{useState,useEffect,useCallback}from 'react'
import {Container,Row,Col,Modal, ModalHeader, ModalBody, Button} from 'reactstrap'
import Netflix from '../../../../images/svg/Netflix.svg'
import Disney from '../../../../images/svg/DisneyPlus.svg'
import Apple from '../../../../images/svg/AppleTV.svg'
import Qiy from '../../../../images/svg/QIY.svg'
import Amazon from '../../../../images/svg/Amazon.svg'
import KKtv from '../../../../images/svg/KKTV.svg'
import LineTV from '../../../../images/svg/LineTV.svg'
import NoImage from '../../../../images/No_image.png'
import axios from 'axios'

const MovieDesc = props => {
  const {details,genres,language,provider,director,subOverview} = props
  const [collapse,setCollapse] = useState(false)
  const [modal,setModal] = useState(false)
  const [video,setVideo] = useState([])
  const toggle = ()=>{
    setModal(!modal)
  }

  const providerMap = str =>{
    const map = {
      'Apple iTunes' : Apple,
      'Amazon Video' : Amazon,
      'Disney Plus' : Disney,
      'Netflix' : Netflix,
      'KKTV' : KKtv,
      'LINE TV' : LineTV,
      'default' : ''
      
    }
    return map[str] || map['default']
  }

  const convertTime = time =>{
    const hour = Math.floor(time / 60)
    const minute = time % 60
    return `${hour}小時${minute}分`
  }

  const collapseHadler = useCallback(()=>{
    setCollapse(!collapse)
  },[collapse])

  useEffect(()=>{
    axios.get(`${process.env.REACT_APP_Url}/${details['type']}/${details['id']}/videos?api_key=${process.env.REACT_APP_Key}&language=zh-TW`).then(res=>{
      console.log(res.data)
      setVideo(res.data.results)
    }).catch(err=>{
      console.log(err)
    })
  },[details])
  return (
    <>
      <Container className='my-5 px-5 cardBG position-relative'>
        <Row>
          <Col md={4}>
            <img className='w-100 detailsImg' src={details['poster_path'] === null ? NoImage : `${process.env.REACT_APP_ImageUrl}${details['poster_path']}`} alt="" />
            {video.length !== 0 && <button onClick={toggle} className='playBtn'>
              <div></div>
            </button>}
          </Col>
          <Col md={8}>
            <div className="text-white d-flex flex-column align-items-start mt-1">
              <div className='d-flex flex-row align-items-end'>
                <div className='mb-1'>
                  {genres.map((i,index)=>{
                    return <span key={index} className='mr-2 detailsType' >{i['name']}</span>
                  })}
                </div>
                <button className='detailsAddBtn'>加入片單</button>
              </div>
              <div className='mt-2 d-flex detailsTitle'>
                <h2>
                  {details['title'] || details['name']}
                </h2>
                <h2>
                  {details['vote_average']}
                </h2>
              </div>
              <div className="cardTitle">
                <span>{details['release_date'] || details['first_air_date']}</span>
                <span>{
                  language.map((i,index)=>{
                    return <span key={index} className={index !== (language.length - 1) ? 'mr-1' : ''}>{i}</span>
                  })
                }</span>
                <span>{details['type'] === 'movie' ? (details['runtime'] <= 59 ? `${details['runtime']}分` : convertTime(details['runtime'])) : `共${details['number_of_seasons']}季，共${details['number_of_episodes']}集`}</span>

              </div>
              <div className="cardTitle mt-4">
                <span>
                  <span className='mr-2'>導演</span>
                  <span>{director.length>0?director[0]['name']:''}</span>
                </span>
              </div>
              <div className="cardTitle mt-4">
                <span>
                  劇情介紹
                </span>
              </div>
              <div className="cardTitle mt-3">
                <p className='ml-3 mb-1'>{subOverview ? (collapse?details['overview']:subOverview) : details['overview']}<b className='ml-2' onClick={collapseHadler}>{collapse?'(關閉更多)':'(觀看更多)'}</b></p>
              </div>
              <div className="cardTitle my-3">
                <span>
                  播放平台
                </span>
              </div>
              <div className="ml-2 detailsPlatform d-flex flex-row">
                {provider === undefined || provider.length === 0? <span>台灣尚未上映</span> : (provider['buy'] || provider['rent'] || provider['flatrate'] || provider['ads']).map((i,index)=>{
                  return <img  className='mr-1' src={providerMap(i['provider_name'])} key={index} alt="" />
                })}

              </div>
            </div>
          </Col>
        </Row>
      </Container>
      <Modal isOpen={modal} toggle={toggle} >
        <div className='mainBG trailerModalHeader' >
          <span onClick={toggle}>X</span>
        </div>
        <ModalBody className='trailerModal'>
          <iframe width="100%" height="100%" allow="fullscreen;"
            src={video.length !== 0 && `https://www.youtube.com/embed/${video[0]['key']}`}>
          </iframe>
        </ModalBody>
        
      </Modal>
    </>
)
}

export default MovieDesc