import React,{useEffect,useState} from 'react'
import {Container,Row,Col} from 'reactstrap'
import { useNavigate } from 'react-router-dom'
import Nav from '../Nav'
import axios from 'axios'

const Banner = props => {
  const {url,apiKey,total} = props
  const [top,setTop] = useState([])
  const [active,setActive] = useState(0)
  const [chose,setChose] = useState(0)

  let navigate = useNavigate()
  const addStr = (str,index)=>{
    return [str.substring(0,index),str.substring(index,str.length)]
  }

  const sortAndCombine = async (a,b)=>{
    const movie = await a
    const tv = await b
    const combine = movie.concat(tv).filter(i=>i['overview'] !== '').sort((a,b)=>{
      return Number(b['vote_average']) - Number(a['vote_average'])
    }).slice(0,total)
    setTop(combine)
  }

  useEffect(()=>{

    const movie = axios.get(`${url}/movie/popular?api_key=${apiKey}&language=zh-TW&page=1&region=TW`).then(result=>{
      const movie = (result.data.results).map(i=>{
        i['type'] = 'movie'
        return i
      })
      return movie
    }).catch(err=>{
      console.log(err)
    })
    
    const tv = axios.get(`${url}/tv/popular?api_key=${apiKey}&language=zh-TW&page=1`).then(result=>{
      const tv = (result.data.results).map(i=>{
        i['type'] = 'tv'
        return i
      })
      return tv
    }).catch(err=>{
      console.log(err)
    })
    sortAndCombine(movie,tv)


  },[])

  useEffect(()=>{
    const intervial = setInterval(()=>{
      setActive((active+1+total)%total)
    },8000)

    return ()=>clearInterval(intervial)
  },[active])
  
  return (
    <>
      <Nav hover={true} padding={true}/>
      <section className="carousel">
        <div className="imgContainer d-flex flex-column flex-nowrap justify-content-end">
          {
            top.map((i,index)=>{
              const overview = addStr(i['overview'].ellipsis(41),20)
              const name = i['title'] || i['name']

              return <div key={index} className={active === index ? 'show carouselItem' : 'carouselItem'}>
                      <img  src={`https://image.tmdb.org/t/p/original${i['backdrop_path']}`} alt="" className='' />
                      <div className="oveyLay"></div>
                      <div className="imgDesc">
                        <h2>{i['vote_average']}</h2>
                        <h3>{name}</h3>
                        <Row>
                          <Col md="12">
                            <p className='m-0'>{overview[0]}</p>
                            <p className='m-0'>{overview[1]}</p>
                          </Col>
                          <Col md="12" className="indexImgBtn d-flex flex-row justify-content-around">
                            {/* <Link to={`../movieDesc/${i['id']}/${i['type']}`}> */}
                              <button onClick={()=>{
                                navigate(`../movieDesc/${i['id']}/${i['type']}`)
                              }}>更多資訊</button>
                            {/* </Link> */}
                            <button data-id={i['id']}>加入片單</button>
                          </Col>
                        </Row>
                      </div>
                    </div>
            })
          }
          <div className="imgNav d-flex flex-row flex-nowrap justify-content-center">
            {
              top.map((i,index)=>{
                return <div key={index} data-key={index} className={active === index ? 'mr-2 chose' : 'mr-2'} onClick={e=>{
                  e.persist()
                  const target = Number(e.target.dataset['key'])
                  setActive(target)
                }}></div>
              })
            }
          </div>
        </div>
      </section>
    </>
  )
}

export default Banner