import React,{useState} from 'react'

const Type_render = props => {
  const {title,content,select,setting,check,current} = props

  return (
    <>
      {title ? 
        <div className="cardTitle ml-3 my-3">
          <span>
            {title}
          </span>
        </div> : ''
      }
      <div className={title ? 'theaterType' : 'theaterFilter'}>
        {content.map((i,index)=>{
          return <span key={index} className={i['id'] === select? 'selected' : ''} onClick={()=>{
            if(check)check(false)
            if(current)current(1)
            setting(i['id'])
          }}>{i['name']}</span>
        })}
      </div>

    </>
  )
}

export default Type_render