import React,{useState,useEffect} from 'react'
import { Container,Row,Col } from 'reactstrap'
import {Link,useNavigate} from 'react-router-dom'
import {FaSearch} from 'react-icons/fa'
import GoogleLogin from 'react-google-login'
import axios from 'axios'

const Nav = props => {
  const {parentCallback,hover,padding,select,initTheater} = props
  const [search,setSearch] = useState(false)
  const [input,setInput] = useState('')
  const [open,setOpen] = useState(false)
  const [target,setTarget] = useState('')
  const navigate = useNavigate()

  const inputHandler = e=>{
    setInput(e.target.value)
  }


  const submitHandler = e=>{
    e.preventDefault()
    if(input === ''){
      alert('請輸入關鍵字')
      return
    }
    // NAV pass params to parent
    setSearch(true);
    if(parentCallback)parentCallback(true);

    // navigate(`../SearchResult/${input}/${target}`)
    navigate(`../SearchResult/${input}/search`)
  }

  const googleSuccess = e=>{
    console.log(e)
  }

  const googleFailure = e=>{
    console.log(e)
  }

  useEffect(()=>{
    const closeMenu = (e)=>{
      if(e.target.id === 'navSelected')return
      setOpen(false)
    }
    window.addEventListener('click',closeMenu)
    return ()=>window.removeEventListener('click',closeMenu)
  },[])


  return (
    <header className={padding ? 'indexPost' : 'indexPost pt-5'}>
      <nav className='sideNav d-flex flex-wrap flex-row py-3 px-4'>  
        <Col md={'7'} className='d-flex justify-content-start'>
          <Link to='/sideProject'>
            <div className="sideImg d-flex mr-3">
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <span className='ml-3'>影片</span>
            </div>
          </Link>
          <form className='d-flex align-items-center pl-2 navSearch' onSubmit={submitHandler}>
            <FaSearch onClick={submitHandler} />
            <input type="text" value={input} 
            placeholder="搜尋劇名/演員"
            onInput={inputHandler}/>
          </form>
        </Col>
        <Col md='5' className='d-flex align-items-center justify-content-end'>
        <GoogleLogin
        clientId={process.env.REACT_APP_Google}
        buttonText='Login'
        onSuccess={googleSuccess}
        onFailure={googleFailure}
        cookiePolicy={"single_host_origin"}
        />
          <ul className="d-flex flex-row flex-nowrap mb-0 navMenu">
            <li className={!hover ? 'navMenuItem' : select === 'movie' ? 'navTheaterSelect' : ''}>
              <Link to='/theater/movie' onClick={()=>{
                if(initTheater)initTheater()
              }}>電影</Link>
            </li>
            <li className={!hover ? 'navMenuItem' : select === 'tv' ? 'navTheaterSelect' : ''}>
              <Link to='/theater/tv' onClick={()=>{
                if(initTheater)initTheater()
              }}>影集</Link>
            </li>
          </ul>
        </Col>
      </nav>
    </header>
  )
}


export default Nav
