import React ,{useState,useEffect}from 'react';
import LineBG from './lineBG';
import AppleBg from './appleBG';
import '../../../sass/page/intro.sass';

const Intro = () => {
  const [scroll,setScroll] = useState(false)
  const [progress,setProgress] = useState(0) 


  useEffect(e=>{
    const onScroll = () =>{ 
      const kv = document.querySelector('.kv').getBoundingClientRect()['y']
      const scrollPage = window.pageYOffset
      const scrollY = window.scrollY
      const height = window.innerHeight
      
      console.log(kv,scrollPage,height)
      
      if(scrollY > 0){
        setScroll(true)
      }else{
        setScroll(false)
      }

      if(scrollPage < height){
        console.log('start')
        
        setProgress(3)
      }else if(scrollPage > height * 2){
        console.log('end')
        setProgress(1)
      }else {
        console.log('check:',3 - 2 * ((scrollPage - height) / height))
        setProgress(3 - 2 * ((scrollPage - height) / height))
      }
    }
    window.addEventListener('scroll',onScroll)
    return () => window.removeEventListener("scroll", onScroll);

  },[progress])
  return (
    <>
      <LineBG scroll={scroll}/>
      <AppleBg progress={progress} />
    </>
  )
}

export default Intro 