import React from 'react';
import {Link} from 'react-router-dom';


const Collapse = props => {
  const {isOpen,setOpen} =  props
  const list = ['簡易資料','BlockStrategy','sideProject','帳號']
  return (
    <div className={isOpen ? 'collapse-nav show' : 'collapse-nav'}>
      <ul className='p-0 m-0 collapse-ul'>
        {
          list.map((i,index)=>{
            const to = `/${index === 0 ? '' : i}`
            return <li key={index} className='mr-3'>
              {index !== 3 ? <Link to={to} className='text-white' onClick={e=>{
                setOpen(false)
              }}>{i}</Link>: <a href="#!" className='text-white' onClick={e=>{
                e.preventDefault()
              }}>{i}</a>}
            </li>
          })
        }
      </ul>
    </div>
  )
}

export default Collapse