import React,{useState} from 'react'
import '../../../sass/Nav/Nav.sass';
import Collapse from '../Collapse';
import CollapseBtn from '..//CollapseBtn';
import logo from '../../../images/BlockStrategy.png';
import logoText from '../../../images/BlockStrategyText.png';
import { Navbar, NavbarBrand} from 'reactstrap';

const Main = () => {
  const [collapsed,setCollapsed] = useState(false)


  const toggleNavbar = ()=>{
    setCollapsed(!collapsed)
  }
  return (
    <>
      <Navbar  expand="md" className='bgcFirst'>
        <NavbarBrand href="/">
          <img src={logo} className="nav_logo img_4rem mr-3" alt="logo" />
          <span className='bg-light p-2 border-radius-1'><img src={logoText} className="nav_logo img_2rem" alt="logo" /></span>

        </NavbarBrand>
        <CollapseBtn toggle={toggleNavbar}/>
        <Collapse isOpen={collapsed} setOpen={setCollapsed}/>
      </Navbar>

    </>


  )
}

export default Main