import React,{useState,useEffect} from 'react'
import IndexList from './indexList'
import {Container,Row,Col} from 'reactstrap'

const MovieTvList = props => {
  const {list} = props
  return (
    <>
      <Container className='indexListContainer'>
        <Row>
          {
            list.map((i,index)=>{
              return <Col md={12} key={index} className='py-5'>
                <h4 className='text-white'>{i['name']}</h4>
                <IndexList url={i['url']} type={i['type']}/>
              </Col>
            })
          }
        </Row>
      </Container>
    </>
  )
}

export default MovieTvList