import React,{useState} from 'react'

const RateBar = props => {
  const {rateBar,setRateBar,rateMove,setRateMove} = props
  const inputMax = 10,thumbWidth = 40
  const style = {
    left:`${rateMove}px`
  }
  return (
    <>
      <div className="cardTitle ml-3 my-3 d-flex flex-row align-items-center ">
        <span>
          評分
        </span>
        <div className="rateBar d-flex flex-row align-items-center ">
          <span>0</span>
          <input type="range" min="0" max="10"  value={rateBar} onInput={e=>{
            const target = e.target
            const width = (target.clientWidth)
            const val = target.value
            const check = (val === '0' || val === '1' ? 35 : (val === '2' || val === '3') ? 34 : (val === '4' || val === '5' || val === '6') ? 32 : val === '10' ? 25 : 31)
            const px = (val * (width - thumbWidth) / inputMax) + check
            setRateMove(px)
            setRateBar(val)
          }}/>
          <span className="rateBarValue" style={style}>{rateBar}</span>
          <span>10</span>
        </div>

      </div>

    </>  
  )
}

export default RateBar