import './sass/App/App.sass';
import Login from './Component/login';
import Nav from './Component/Nav/Main';
import Intro from './Component/Page/Intro/Main';
import BS from './Component/Page/BS/Main';
import SideProject from './Component/Page/SideProject/index/Main';
import MovieDesc from './Component/Page/SideProject/movieDesc/Main';
import SearchResult from './Component/Page/SideProject/searchResult/Main';
import Theater from './Component/Page/SideProject/theater/Main'

import { BrowserRouter as Router,Routes,Switch,Route } from "react-router-dom";

const App = () => {
  const state = false

  String.prototype.ellipsis = function(n){
    const str = this.toString()
    return (str.length > n) ? `${str.substr(0,n-1)}...`:str
  }

  if(!state){
    return (

      <div className= "App">
        <Router >
          <Nav/>
          <section>
            <Routes>
              <Route path="/" exact element={<Intro/>}></Route>
              <Route path="/BlockStrategy" element={<BS/>}></Route>
              <Route path="/sideProject" element={<SideProject/>}></Route>
              <Route path="/MovieDesc/:id/:type" element={<MovieDesc/>}></Route>
              <Route path="/SearchResult/:id/:type" element={<SearchResult/>}></Route>
              <Route path="/Theater/:type" element={<Theater/>}></Route>
            </Routes>
          </section>
        </Router>
      </div>
    )
  }else{

    return (
      
      <div className= "loginBg App d-flex align-items-center">
        <Login />
      </div>
    );
  }
}

export default App;
