import React,{useEffect,useState} from 'react'
import {useParams,Link} from "react-router-dom"
import Cast from '../movieDesc/Cast'
import RenderResult from '../RenderResult'
import {Container,Row,Col} from 'reactstrap'
import axios from 'axios'

const SearchContainer = props => {
  const {search,setSearch} = props
  const [target,setTarget] = useState(false)
  const [cast,setCast] = useState('')
  const [content,setContent] = useState([])
  const [totalPage,setTotalPage] = useState(1)
  const [current,setCurrent] = useState(1)
  const [loading,setLoading] = useState(false)
  const {id,type}= useParams()

  
  
  
  useEffect(()=>{
    if(search){
      setContent([])
      setTotalPage(1)
      setCurrent(1)
      // setLoading(false)
      setSearch(false)
    }
    if(type === 'cast'){
      const axiosCredits = axios.get(`${process.env.REACT_APP_Url}/person/${id}/combined_credits?api_key=${process.env.REACT_APP_Key}&language=zh-TW`)
      const axiosCast = axios.get(`${process.env.REACT_APP_Url}/person/${id}?api_key=${process.env.REACT_APP_Key}&language=zh-TW`)

      axios.all([axiosCredits,axiosCast]).then(axios.spread((...res)=>{
        const itemCredits = res[0].data
        const itemCast = res[1].data
        setCast(itemCast['name'])
        setContent(itemCredits['cast'])
      }))
    }else{
      const getMoreList = ()=>{
        setLoading(true)
        axios.get(`${process.env.REACT_APP_Url}/search/${!target ? 'multi' : 'person'}?api_key=${process.env.REACT_APP_Key}&language=zh-TW&query=${id}&page=${current}&include_adult=false&region=TW`).then(res=>{
          setContent([...content,...res.data.results])
          setTotalPage(res.data.total_pages)
          setLoading(false)
        }).catch(err=>{
          console.log(err)
        })
      }
      getMoreList()
      return ()=>getMoreList
    }
  },[id,type,current,target,search])

  return (
    <Container className='mt-5'>
      <Row>
        <Col md='12' className='px-5'>
          <div className="searchTitle">
            {type === 'cast' ? <span>{cast} 的其他作品</span>: <div>
            <div className='mb-2'>「{id}」相關搜尋結果</div>
            <div className="d-flex">
              <button onClick={()=>{
                setContent([])
                setCurrent(1)
                setTarget(false)
              }} className={!target ? 'searchBtnColor searchBtn' : 'searchBtn'}>片名</button>
              <button onClick={()=>{
                setContent([])
                setCurrent(1)
                setTarget(true)
              }} className={target ? 'searchBtnColor searchBtn' : 'searchBtn'}>演員</button>
            </div>
              </div>}            
          </div>
        </Col>
        <RenderResult content={content} target={target} type={type} loading={loading} totalPage={totalPage} current={current} setCurrent={setCurrent}/>
      </Row>
    </Container>
  )
}

export default SearchContainer