import React ,{useState,useEffect}from 'react'


const LineBG = props => {
  // const [scroll,setScroll] = useState(false)
  const {scroll} = props
  const [active,setActive] = useState(0)
  const [preactive,setPreactive] = useState(0)
  const total = 5,animationDuration = 10

  const img = n =>{
    return {
      backgroundImage:`url(https://picsum.photos/1920/1000?random=${n})`,
      animationDuration:`${animationDuration}s`,
      // animationDelay:`${(n-1)*animationDuration/2}s`,
    }
  }

  useEffect(()=>{

    const interval = setInterval(()=>{
      setPreactive(active)
      setActive((active+1+total)%total)
    },animationDuration/2*1000)
    return ()=>clearInterval(interval)
  },[active,preactive])
  return (
    <div className={scroll ? 'kv scroll' : 'kv'}>
      <ul className="kvList">
        {
          [...Array(total)].map((i,index)=>{
            return <li key={index} style={img(index)} className={active === index || preactive === index ? 'animate' : ''}></li>
          })
        }
      </ul>
    </div>
  )
}

export default LineBG