import React from 'react'
import {Container,Row,Col} from 'reactstrap'
import { Link } from 'react-router-dom'
import userNone from '../../../../images/userQuestion.webp'

const Cast = props => {
  const {cast,bg,center} = props
  return (
    <Container className={bg ? "my-4 detailCardBG" : "my-4"}>
      <Row>
        <Col md={12} className={center ? 'd-flex flex-row flex-wrap justify-content-center' : 'd-flex flex-row flex-wrap'}>
          {cast.map((i,index)=>{
            return <Link key={index} to={`../SearchResult/${i['id']}/cast`} className='detailCast mx-2 mb-4 d-flex flex-column align-items-center text-white' >
                    {i['profile_path'] === null ? <img src={userNone} alt="" /> : <img src={`https://image.tmdb.org/t/p/original${i['profile_path']}`} alt="" />}
                    <span className='text-center' >{i['name']}</span>
                  </Link>
          })}
        </Col>
      </Row>
    </Container>
  )
}

export default Cast